.App {
  margin: 2px;
}

.bar-display {
  width: 100%;
  height: 35px;
  background-color: #858693;
}

.bar-display .writting {
  width: 98%;
  margin: auto;
  color: white;
  text-align: end;
  line-height: 35px;
  padding-right: 10px;
}

.button {
  display: inline-block;
  text-align: center;
  line-height: 35px;
  border: 1px solid gray;
  cursor: pointer;
}

.section-calc {
  display: grid;
  grid-template-columns: 40% 50%;
}

.calculator {
  width: 300px;
  min-height: 100px;
  margin: auto;
}

.navbar {
  display: grid;
  grid-template-columns: auto auto;
  margin-bottom: 20px;
}

.active {
  color: green;
  text-decoration: none;
}

.navbar h1 {
  font-size: 2.5em;
  padding-left: 10px;
  line-height: 1.5em;
}

.navbar .link {
  padding-top: 35px;
  text-align: end;
}

.navbar .link ul {
  list-style: none;
  margin: 0;
  justify-content: end;
}

.navbar div ul li {
  display: inline-block;
  margin: 10px;
  font-size: 19px;
}

li a {
  text-decoration: none;
  color: black;
}

li a:hover {
  color: green;
}
